body {
  margin: 0;
  padding: 0;
  background-color: #ED9390;
  color: #43281C;
  font-size: 16px;
}

a {
  color: #48392A;
}

.App {
  text-align: center;
}

.display-cost {
  text-align: center;
  color: #EDF7F6;
  padding: 1.2rem;
}

.display-cost h2 {
  font-size: 3rem;
  font-weight: normal;
}

.input-section {
  padding: 1rem;
  color: #EDF7F6;
}

.input-section input {
  padding: 0.5rem;
  margin-left: 0.5rem;
  border-radius: 0.5rem;
  border-color: #48392A;
  border-width: 1px;
}
